.side-drawer {
    height: 100%;
    background: white;
    box-shadow: 2px 0px 8px rgba(0,0,0,0.5);
    position: fixed;
    top:0;
    left:0;
    width: 200px;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out   
}

.side-drawer.open {
    transform: translateX(0);
}



.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: top;


}

.side-drawer li {
    margin: 1rem 0;

}

.side-drawer a {
    color: black;
    text-decoration: none;
    font-size: 1.2rem;


}

.side-drawer a:hover,
.side-drawer a:active {
    color: #37c8c6;
}


.fonts1 {
    font-family: 'Titillium Web', sans-serif;
}